@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Outfit:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Racing+Sans+One&display=swap');
$white : #fff;
$yellow : #f7bf31;

* {box-sizing: border-box;}
body {font-family: 'Noto Sans KR', sans-serif; margin: 0; overflow-x: hidden;}
h1,h2,h3,h4,h5,h6, figure, p {margin: 0;}
a {text-decoration: none;}
ul, li {list-style: none; padding: 0; margin: 0;}
img {vertical-align: middle;}
table {border-collapse: collapse;}
::selection {background-color: $yellow;color: #322738;}
header {
    width: 100%; position: fixed; z-index: 10; top: 75px; width: 1470px; left: 50%; transform: translateX(-50%); display: flex; justify-content: space-between; align-items: center;
    h1 {
        width: 100px; height: 100px; vertical-align: top;
        a {width: 100%; height: 100%; line-height: 100px; font-size: 0; background: $yellow; display: block; text-align: center;}
    }
    .menu {
        ul {
            display: flex; justify-content: space-between; align-items: center; height: 50px; padding: 10px 0; /*background: rgba(0,0,0,.5);*/
            li {
                padding-left: 30px;
                a {color: rgba(255,255,255,.5); font-size: 25px; font-family: 'Outfit', sans-serif; font-weight: 400;}
                &::before {content: url('../img/ico_mn_2.png'); display: inline-block; vertical-align: middle;}
            }
            li.on {
                a {color: $yellow;}
                &::before {content: url('../img/ico_mn_1.png'); display: inline-block;}
            }
        }
        p {display: none;}
    }

}

header.active {
    h1 a {background: $white;}
    .menu ul {display: none;}
    .menu p {display: block; width: 60px; height: 60px; border-radius: 50%; background: $white; text-align: center; line-height: 60px;}
    .menu p span {display: block; width: 100%; height: 100%;}
}
header.none {display: none;}

// scroll

.content div{position:relative}
.content div p{position:absolute;top:50%;margin-top:-40px;text-align:center;width:100%;font-size:80px;font-weight:bold;color: $white;}
.content .cnt01{}
.content .cnt02{background-color:#000}
.content .cnt03{background-color:#000}
.black {background: #000;}
.inner {width: 1470px; margin: 0 auto;}

main {
    .visual {
        width: 100%; /*height: 100vh;*/ position: relative;
        img {object-fit: cover; object-position: center;}
        .per {width: 100%; position: absolute; left: 0; bottom: 0;}
        .tit {
            position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%); width: 100%;
            h2 {text-align: center; color: $white; font-size: 120px; font-family: 'Outfit', sans-serif; font-weight: 700; line-height: 136px; text-shadow: 15px 15px 15px rgba(0,0,0,0.35);}
            // h2 span {opacity: 0; transform: translateY(100%);}
            // h2.active {opacity: 1; transform: translateY(0%);}
            h2 div {display:inline-block; overflow:hidden; white-space:nowrap;}
            h2 div:first-of-type {    /* For increasing performance 
                                     ID/Class should've been used. 
                                     For a small demo 
                                     it's okaish for now */
                animation: showup 7s infinite;
            }
            h2 div:last-of-type {width:0; animation: reveal 7s infinite;}
            h2 div:last-of-type span {margin-left:-355px; animation: slidein 7s infinite;}

            @keyframes showup {
                0% {opacity:0;}
                20% {opacity:1;}
                80% {opacity:1;}
                100% {opacity:0;}
            }

            @keyframes slidein {
                0% { margin-left:-800px; }
                20% { margin-left:-800px; }
                35% { margin-left:0px; }
                100% { margin-left:0px; }
            }

            @keyframes reveal {
                0% {opacity:0;width:0px;}
                20% {opacity:1;width:0px;}
                30% {width:700px;}
                80% {opacity:1;}
                100% {opacity:0;width:700px;}
            }

            h3 {text-align: center; width: 600px; margin: 0 auto; line-height: 136px;}
            h3 div {width:0; animation: reveal1 7s infinite; display: inline-block; overflow:hidden; white-space:nowrap;}
            p {
                font-weight: 300; font-size: 24px; line-height: 50px; text-align: center; margin-top: 180px; animation: txt2 7s infinite;
                span {font-weight: 500;}
            }
        }
        
    }
    @keyframes text {
        0% {margin-bottom: -40px;}
        30% {letter-spacing: 25px; margin-bottom: -40px;}
        85% { letter-spacing: 8px; margin-bottom: -40px;}
      }
    
    @keyframes reveal1 {
        0% {opacity:0;width:0px;}
        20% {opacity:1;width:0px;}
        30% {width:80%;}
        80% {opacity:1;}
        100% {opacity:0;width:80%;}
    }
    @keyframes txt2 {
        0% {opacity:0; width:100%; transform: translateY(100px);}
        20% {width:100%; opacity:0; transform: translateY(100px);}
        30% {width:100%; opacity:1; transform: translateY(0);}
        80% {opacity:1;}
        100% {opacity:0;width:100%;}
    }
    .slider_w {
        top: 50%; transform: translateY(-50%); position: relative;
        .slide {
            display: flex; align-items: center; justify-content: flex-start;
            .txt {
                color: $white; padding-left: 60px; margin-top: -145px;
                h3 {font-size: 64px; font-family: 'Noto Sans KR', sans-serif; font-weight: 700; position: relative; margin-bottom: 25px;}
                h3::before {position: absolute; content: ''; display: block; width: 60px; height: 4px; background: $white; top: calc(50% - 2px); left: -90px;}
                li {font-size: 18px; font-family: 'Noto Sans KR', sans-serif; font-weight: 300; line-height: 38px;}
                li::before {content: url('../img/slide_bullet.png');}
                li span {font-weight: 700;}
                a {display: inline-block; width: 185px; height: 56px; border: 1px solid $white; margin-top: 45px; line-height: 54px; text-align: center; color: #fff; font-size: 16px; font-weight: 300; position: relative; padding-right: 30px;}
                a::after {content: ''; position: absolute; width: 30px; height: 8px; background: url('../img/arrow_more.png'); top: calc(50% - 4px); right: 30px;}
            }
        }
        .pager {
            display: flex; justify-content: center; align-items: center; position: absolute; left: calc(50% + 273px); bottom: 80px;
            div {
                font-size: 18px; color: $white; font-weight: 300; padding: 0 15px; width: auto;
                b {font-size: 24px; font-weight: 700;}
            }
            
        }
    }
    
    .info_w {
        width: 100%; height: 100%; display: flex;
        .left, .right {width: 50%; height: 100%;}
        .left {
            color: $white; padding: 220px 0 0 150px;
            h3 {font-size: 64px; font-family: 'Noto Sans KR', sans-serif; font-weight: 700; position: relative; margin-bottom: 25px;}
            h3::before {position: absolute; content: ''; display: block; width: 60px; height: 4px; background: $white; top: calc(50% - 2px); left: -90px;}
            li {font-size: 18px; font-family: 'Noto Sans KR', sans-serif; font-weight: 300; line-height: 38px;}
            li::before {content: url('../img/slide_bullet.png');}
            li span {font-weight: 700; padding-right: 23px;}

        }
        .right {
            background: #322738; position: relative;
            h3 {font-size: 110px; font-family: 'Outfit', sans-serif; position: absolute;}
            h3.full {color: $white; top: 390px; left: -40px;}
            h3.line {text-shadow:-1px -1px 0 $white, 1px -1px 0 $white, -1px 1px 0 $white, 1px 1px 0 $white; color: #322738; top: 510px; left: 240px;}
            .cir1 {position: absolute; width: 200px; height: 200px; border-radius: 50%; background: #b58fcc; top: 100px; left: 100px;}
            .cir2 {position: absolute; width: 400px; height: 400px; border-radius: 50%; background: #592966; top: 480px; left: 460px;}
        }
    }

}

.list_w {
    padding-top: 280px;
    .tit {
        display: flex; position: relative;
        h2 {
            font-family: 'Racing Sans One', cursive; font-size: 110px; line-height: 100px; color: $white; letter-spacing: 10px;
            span {text-shadow: -1px 0 $white, 0 1px $white, 1px 0 $white, 0 -1px $white; color: #000; padding-left: 300px;}
        }
        p {color: $white; padding: 100px 0 0 150px; font-size: 24px; line-height: 36px;}
        
        .btns {
            position: absolute; top: 300px; right: 0; z-index: 10; width: 100px;
            span {color: $white; cursor: pointer; padding-left: 20px; width: 50px;}
            span img {width: 30px;}
        }
    }
    .list {
        padding-top: 100px; display: flex; flex-wrap: wrap;
        .thumb {
            height: 760px; position: relative; margin-right: 30px;
            .img {}
            .txt {
                padding-top: 40px;
                h3 {font-size: 30px; font-weight: 500; color: $white; line-height: 36px; word-break: break-all; width: 340px;}
                .device {font-size: 24px; font-weight: 300; color: $yellow;}
                .date {font-size: 16px; font-weight: 300; color: $white; line-height: 30px; position: absolute; bottom: 75px;}
                .date span {padding-right: 35px;}
            }
        }
        a:nth-child(4n) .thumb {margin-right: 0;}
    }
    .slide_w {padding-bottom:100px;}
    .slide {
        display: flex; padding-top: 160px;
        .txt {
            color: $white;
            h3 {
                font-size: 64px; font-weight: 700; padding: 80px 0 0 60px; position: relative; width: 600px;
                &::before {position: absolute; content: ''; width: 60px; height: 4px; background: $white; top: 135px; left: -30px;}
            }
            dl {padding: 25px 0 0 60px; margin: 0; display: flex; flex-wrap: wrap; font-size: 18px;}
            dl dt {display: inline-block; width: 35%; line-height: 40px; font-weight: 700;}
            dl dd {display: inline-block; width: 65%; margin: 0; line-height: 40px; font-weight: 300;}
            a {display: inline-block; width: 185px; height: 55px; border: 1px solid $white; text-align: center; line-height: 55px; color: $white; margin: 30px 0 0 60px;}
        }
    }
    .slide_w.active {display: block;}
}
.swiper-button-next, .swiper-button-prev {color: $white !important;}

.detail_w {
    position: relative; z-index: 1;
    .bg {
        height: 150vh; background-size: cover; opacity: .5; z-index: 1; overflow: hidden;}
    .con_w {
        z-index: 10; position: absolute; top: 0;
        .g_bg {background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 50%, rgba(0,0,0,0) 100%); padding: 655px 100px 0 100px;}
        .btn_w {width: 285px; height: 100px; background: $white; text-align: center; float: right;}        
        .btn_w a {color:  #000; font-size: 24px; font-weight: 500;  line-height: 100px;}
        .con {width: 100%; padding: 100px 125px; background: $white; clear: both;}
        .con .txt {position: relative; padding-bottom: 50px;}
        .con .txt h3 {font-size: 64px; font-weight: 700;}
        .con .txt p {font-size: 24px; font-weight: 500; line-height: 40px; padding-top: 30px;}
        .con .txt p span {padding-right: 120px;}
        .con .txt a {color: $white; background: #322738; display: inline-block; width: 200px; height: 75px; line-height: 75px; text-align: center; font-size: 24px; position: absolute; top: 125px; right: 0;}
        .con .img {width: 100%}
        .con .img img {width: 100%;}
    }
}

.about_w {
    padding-top: 280px;
    .tit {
        display: flex;
        h2 {
            font-family: 'Racing Sans One', cursive; font-size: 110px; line-height: 100px; color: $white; letter-spacing: 10px;
            span {text-shadow: -1px 0 $white, 0 1px $white, 1px 0 $white, 0 -1px $white; color: #000; padding-left: 300px;}
        }
    }
    .scr {
        position: fixed; color: $white; width: 20px; height: 40px; border-radius: 10px; border: 1px solid $white; left: 50%; margin-left: 700px; bottom: 100px;
        span {font-size: 0;}
        span:before {content: ''; display: block; width: 7px; height: 7px; border-radius: 3px; background: $white; top: 5px; left: 5px; position: absolute; animation: ball 2s infinite;}
    }
    .scr.none {display: none;}
    @keyframes ball{
        0% {top: 5px;}
        50% {top: 15px;}
        100% {top: 5px;}
    }
    .about_01 {
        .inner {display: flex; justify-content: space-between; padding: 70px 0 150px 0; height: 2500px;}
        .img, .txt {width: 650px;}
        .img {text-align: right; opacity: 0; padding-top: 175px; transition: 1s ease-in-out;}
        .img img {width: 50%;}
        .img.active {position: sticky; left: 0; top: 0; opacity: 1;}
        .img.active img {width: 100%;}
        .txt {
            padding-top: 300px; opacity: 0; transition: 1s ease-in-out;
            h3 {font-family: 'Racing Sans One', cursive; font-size: 64px; color: $white; padding: 160px 0 90px 0;}
            ul.info li {color: $white; font-size: 20px; font-weight: 400; line-height: 50px;}
            ul.info li span {display: inline-block; width: 110px; color: $yellow;}
            ul.info li span.long {width: inherit;}
            ul.info li span.line {color: $white; line-height: 36px; width: inherit;}

            ul.skill {margin: 0 auto; padding: 0 0 120px 0;}
            ul.skill li {position: relative; padding-left: 85px; color: $white; font-size: 16px; font-weight: 300; line-height: 30px; margin-bottom: 40px;}
            ul.skill li span {color: $yellow; font-size: 20px; font-weight: 700; display: block;}
            ul.skill li::before {content: ''; position: absolute; width: 65px; height: 65px; border-radius: 50%; background-color: $yellow; background-position: center; background-repeat: no-repeat; display: block; top: -2px; left: 0;}
            ul.skill li:nth-of-type(1):before {background-image: url('../img/skill_01.png');}
            ul.skill li:nth-of-type(2):before {background-image: url('../img/skill_02.png');}
            ul.skill li:nth-of-type(3):before {background-image: url('../img/skill_03.png');}
            ul.skill li:nth-of-type(4):before {background-image: url('../img/skill_04.png');}
            ul.skill li:nth-of-type(5):before {background-image: url('../img/skill_05.png');}
            ul.skill li:nth-of-type(6):before {background-image: url('../img/skill_06.png');}

        }
        .txt.active {position: sticky; left: 0; top: 0; opacity: 1; padding-top: 160px;}
    }
    .about_02 {
        
        .bg_w {height: inherit;}
        .bg {
            background-image : url('../img/about_02.png'); background-position: center; background-repeat: no-repeat; /*background-attachment:fixed;*/ overflow: hidden; 
            height : 330px; overflow : auto; width : 100%; position: sticky; top: 0; z-index: 1;
            h3 {text-align: center; padding: 130px 0 20px; font-size: 28px; color: $white;}
            p {text-align: center; font-size: 24px;}
            p a {color: $white; display: inline-block; width: 350px; height: 50px; line-height: 50px; border-radius: 25px; background: $yellow; transition: all 1s;}
            p a:hover {color:$yellow; background: $white;}
        }
        
    }
    .about_03 {
        clear: both; display: block; padding-top: 0px;
        .inner {display: flex; justify-content: space-between; padding: 70px 0 150px 0;}
        .img, .txt {width: 650px}
        .txt {
            h3 {font-family: 'Racing Sans One', cursive; font-size: 64px; color: $white; padding: 45px 0 90px 0; text-align: right;}
            li {color: $white; font-size: 20px; font-weight: 400; line-height: 40px; text-align: right; padding-bottom: 20px;}
            li span {display: inline-block; color: $yellow; display: block;}
        }
    }
}

/* tablet */
@media (min-width:769px) and (max-width:1500px) {
    header {
        width: 100%; padding: 0 2em; top: 2em;
        h1 {width: 3em; height: 3em;}
        .menu ul li a {font-size: 1.5em;}
    }
    header.active .menu p {width: 3.5em; height: 3.5em; line-height: 3.5em;}
    main{
        .visual {
            height: 100vh;
            .tit {
                h2 {font-size: 5em; line-height: 1.2em;}
                h3 {width: 30em;}
                p {font-size: 1.5em; margin-top: 6em;}
            }
            img {width: 100%; height: 100%;}
            .per img {width: 100%;}
        } 
    }
    @keyframes reveal {
        0% {opacity:0;width:0px;}
        20% {opacity:1;width:0px;}
        30% {width:5.5em;}
        80% {opacity:1;}
        100% {opacity:0;width:5.5em;}
    } 
    .inner {width: 100%; padding: 0 2em;}
    .list_w {
        padding-top: 13em;
        .tit {
            display: block;
            h2 {
                font-size: 6em; line-height: 1em;
                span {padding-left: 2.7em;}
            }
            p {font-size: 1.5em; padding: 1em 0 0 0;}
            .btns {top: 15em;}
        }
        .list {
            padding-top: 0; justify-content: space-between;
            a {width: 23%;}
            .thumb {
                width: 100%; height: 40.5em; margin-right: 0;
                .img {width: 100%;}
                .img img {width: 100%;}
                .txt {
                    width: 100%;
                    h3 {width: 100%; font-size: 1.7em; line-height: 1.3;}
                    .device {font-size: 1.25em;}
                    .date {font-size: 1em; bottom: 2.5em;}
                    .date span:nth-of-type(1) {display: block;}
                }
            }
        }
        .slide {
            padding-top: 10em;
            .img {
                width: 60%;
                img {width: 100%;}
            }
            .txt {
                width: 40%;
                h3 {
                    font-size: 3em; padding: 1em 0 0 1.25em; width: 100%; padding-right: 10%;
                    &::before {top: 1.75em;} 
                }
                dl {
                    font-size: 1.125em; padding: 1.25em 0 0 3.75em;
                    dt, dd {line-height: 2.22em;}
                }
                a {width: 11.5em; height: 3.5em; line-height: 3.5em;}
            }
        }
    }

    .detail_w {
        .bg {
            img {width: 100%;}
        }
        .con_w {
            .g_bg {padding: 40em 5em 0 5em;}
            .btn_w {
                width: 15em; height: 4em; line-height: 4em;
                a {font-size: 1.5em; line-height: 2.667em;}
            }
            .con {
                padding: 4em 5em;
                .txt {
                    padding-bottom: 3.125em;
                    h3 {font-size: 4em;}
                    p {font-size: 1.5em; line-height: 1.667em; padding-top: 1.5em;}
                }
                
            }
        }
    }

    .about_w {
        padding-top: 13em;
        .tit {
            h2 {font-size: 6em; line-height: 1;}
            h2 span {padding-left: 2.7em;}
        }
        .scr {margin-left: calc(50% - 5em); bottom: 3em;}
        .about_01 {
            .inner {
                width: 100%; padding: 0 2em;
                .img_w, .txt_w {width: 50%;}
                .img, .txt {width: 100%;}
                .img {padding-top: 8em;}
                .txt {
                    padding-left: 4em;
                    h3 {padding: 2.5em 0 1.25em 0; font-size: 4em;} 
                    ul.info li {font-size: 1.25em; line-height: 2.25;}
                    ul.info li span.line {line-height: 1.75;}

                    ul.skill li {font-size: 1em; margin-bottom: 2em;}
                    ul.skill li::before {width: 4em; height: 4em;}
                }
                .txt.active {padding-top: 10px;}
            }
        }
        .about_03 {
            .inner {
                width: 100%; padding: 0 2em;
                .img, .txt {width: 50%;}
                .img img {width: 100%;}
                .txt {
                    padding-right: 4em;
                    h3 {font-size: 4em; padding: 0.65em 0;}
                    li {font-size: 1.25em; line-height: 1.5; padding-bottom: 0.75em;}
                }
            }
        }
    }
}

/* mobile */
@media (max-width:768px) {
    header {
        width: 100%; padding: 0 1em; top: 1em;
        h1 {
            width: 4em; height: 4em; font-size: 1em; line-height: 4;
            a {line-height: 4em; font-size: 1em;}
            a img {vertical-align: middle; width: 75%;}
        }
        .menu {
            ul li {padding-left: 1em;}
            ul li a {font-size: 1em; letter-spacing: -0.05em;}
        }
    }
    main {
        .visual {
            overflow: hidden;
            .tit {
                transform: translate(-50%, -80%);
                h2 {font-size: 3.5em; line-height: 1.25;}
                h3 {width: 75%; line-height: 2.5em;}
                p {font-size: 1em; line-height: 2; margin-top: 8em;}
            }
        }
    }
    @keyframes reveal {
        0% {opacity:0; width:0px;}
        20% {opacity:1; width:0px;}
        30% {width:5.5em;}
        80% {opacity:1;}
        100% {opacity:0; width:5.5em;}
    }
    .inner {width: 100%; padding: 0 1em;}
    .list_w {
        padding-top: 7em;
        .tit {
            display: block;
            h2 {
                font-size: 2em; line-height: 1;
                span {padding-left: 4em;}
            }
            p {font-size: 1em; padding: 1em 0 0 0; line-height: 1.5;}
            .btns {
                top: 9em; width: 5em;
                span {
                    width: 2.5em; padding-left: 1em; display: inline-block;
                    img {width: 100%;}
                }
            }
        }
        .slide_w {padding-bottom: 1em;}
        .slide {
            padding: 5em 1em; flex-direction: column;
            .img {
                width: 100%;
                img {width: 100%;}
            }
            .txt {
                h3 {
                    font-size: 2em; width: 100%; padding: 0.5em;
                    &::before {display: none;}
                }
                dl {
                    padding: 0 1.25em; font-size: 1em;
                    dt {width: 40%;}
                    dd {width: 60%;}
                }
                a {margin: 1em 0 0 1em; width: calc(100% - 2em); height: 3em; font-size: 1.25em; line-height: 3;}
            }
        }
        .list {
            justify-content: space-between; padding-top: 3em;
            a {width: 48%;}
            .thumb {
                width: 100%; height: inherit; margin-right: 0;
                .img {width: 100%;}
                .img img {width: 100%;}
                .txt {
                    width: 100%; padding-top: 1em;
                    h3 {width: 100%; font-size: 1.7em; line-height: 1.3;}
                    .device {font-size: 1.25em;}
                    .date {font-size: 1em; position: relative; bottom: auto; padding-bottom: 1em;}
                    .date span:nth-of-type(1) {display: block;}
                }
            }
        }
    }
    .detail_w {
        .bg {
            img {width: 100%;}
        }
        .con_w {
            .g_bg {padding: 20em 1em 0 1em;}
            .btn_w {
                width: 35%; height: 3em;
                a {font-size: 1em; line-height: 3;}
            }
            .con {
                padding: 2em;
                .txt {
                    h3 {font-size: 2em;}
                    p {
                        font-size: 1em; line-height: 2; padding-top: 1em;
                        span:nth-of-type(1) {display: block;}
                    }
                    a {width: 35%; height: 3em; line-height: 3; font-size: 1em; top: 7em;}
                }
