  0% {
    opacity: 0;
    width: 0px;
  }
  20% {
    opacity: 1;
    width: 0px;
  }
  30% {
    width: 700px;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    width: 700px;
  }
}

